import loadingGif from '@/assets/loading.gif';
import img from '@/assets/index.svg';
import logo from '@/assets/logo.svg';
import bg from '@/assets/bg1.png';

const container = document.getElementById('app');

if (container) {
  container.innerHTML = `
<style>
html,body{margin:0;height:100%;}
#app{display:flex;height:100%;flex-direction:column;align-items:center;background-color:#23232D;background-image:url(${bg});background-size:50px 28px;}
#loadingGif{position:absolute;width:120px;top:328px;left:50%;margin-left:-60px}
#gradient{position:absolute;top:0;left:0;width:100%;height:100%;background:linear-gradient(0deg,#23232D,transparent);}
#img{width:304px;height:272px;margin-top:110px;}
#logo{position:relative;width:164px;height:68px;margin-top:135px;}
</style>
<img id="img" src="${img}"/>
<div id="gradient"></div>
<img id="logo" src="${logo}"/>
<img id="loadingGif" src="${loadingGif}"/>
`;
}
